import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import "../css/inventory-list.css";

const Exercise = props => (
  <tr>
    <td>{props.exercise.item}</td>
    <td><img src={props.exercise.image} alt={props.exercise.item} width="100%" height="auto" /></td>
    <td>{props.exercise.description}</td>
    <td>{props.exercise.price}</td>
    <td>{props.exercise.size}</td>
    <td>{props.exercise.color}</td>
    <td>{props.exercise.sku}</td>
    <td>{props.exercise.date.substring(0,10)}</td>
    <td>
      <Link to={"/edit/"+props.exercise._id}>edit</Link> | <a href="#" onClick={() => { props.deleteExercise(props.exercise._id) }}>delete</a>
    </td>
  </tr>
)

export default class ExercisesList extends Component {
  constructor(props) {
    super(props);

    this.deleteExercise = this.deleteExercise.bind(this)

    this.state = {exercises: []};
  }

  componentDidMount() {
    axios.get('http://localhost:5000/exercises/')
      .then(response => {
        this.setState({ exercises: response.data })
      })
      .catch((error) => {
        console.log(error);
      })
  }

  deleteExercise(id) {
    axios.delete('http://localhost:5000/exercises/'+id)
      .then(response => { console.log(response.data)});

    this.setState({
      exercises: this.state.exercises.filter(el => el._id !== id)
    })
  }

  exerciseList() {
    return this.state.exercises.map(currentexercise => {
      return <Exercise exercise={currentexercise} deleteExercise={this.deleteExercise} key={currentexercise._id}/>;
    })
  }

  render() {
    return (
      <div className="inventory-list-container">
        <table className="table">
          <thead className="thead-light">
            <tr>
              <th>Item</th>
              <th>Image</th>
              <th>Description</th>
              <th>Price</th>
              <th>Size</th>
              <th>Color</th>
              <th>SKU</th>
              <th>Date</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            { this.exerciseList() }
          </tbody>
        </table>
      </div>
    )
  }
}